"use client";

import React, { useState, useMemo } from "react";
import { Button } from "@/components/ui/button";
import { CameraFeed } from "./CameraFeed";
import { motion } from "framer-motion";

const url: string = "/video.mp4";
interface Props {
  id: string;
  name: string;
  posterUrl: string;
  dataAiHint: string;
}

const ALL_CAMERAS_DATA: Props[] = [
  { id: "cam1", name: "Front View", posterUrl: url, dataAiHint: "front view" },
  {
    id: "cam2",
    name: "Left Side View",
    posterUrl: url,
    dataAiHint: "left side view",
  },
  {
    id: "cam3",
    name: "Right Side View",
    posterUrl: url,
    dataAiHint: "right side view",
  },
  { id: "cam4", name: "Top View", posterUrl: url, dataAiHint: "top view" },
];

export function VideoPlayer() {
  const [mainCamera, setMainCamera] = useState<string>("cam1");

  const currentCamera = useMemo(() => {
    return ALL_CAMERAS_DATA.find((camera) => camera.id === mainCamera);
  }, [mainCamera]);

  const selectCamera = (id: string) => {
    setMainCamera(id);
  };

  if (!currentCamera) {
    return <div>Camera not found!</div>;
  }

  // Filter out the current camera from the list when rendering the second div
  const remainingCameras = ALL_CAMERAS_DATA.filter(camera => camera.id !== mainCamera);

  return (
    <>
      <motion.div
        key={mainCamera}
        className="w-96 h-96"
        initial={{ opacity: 0, x: -100 }}
        animate={{ opacity: 1, x: 0 }}
        exit={{ opacity: 0, x: 100 }}
        transition={{ duration: 0.5 }}
      >
        <CameraFeed
          dataAiHint={currentCamera.dataAiHint}
          id={currentCamera.id}
          posterUrl={currentCamera.posterUrl}
          name={currentCamera.name}
        />
        <span>{currentCamera.name}</span>
      </motion.div>
      <div className="w-96">
        {remainingCameras.map((camera) => (
          <div key={camera.id}>
            <CameraFeed
              dataAiHint={camera.dataAiHint}
              id={camera.id}
              posterUrl={camera.posterUrl}
              name={camera.name}
            />
            <button onClick={() => selectCamera(camera.id)}>
              Switch to {camera.name}
            </button>
          </div>
        ))}
      </div>
    </>
  );
}
